.selectable-chip-text {
  margin-bottom: 2px;
  font-family: "Abhaya Libre", serif;
  font-size: 24px;
  font-weight: 600;
  color: #a93e3c !important;
}

.selectable-chip-text-selected {
  margin-bottom: 2px;
  font-family: "Abhaya Libre", serif;
  font-size: 24px;
  font-weight: 600;
}

.selectable-chip-text-roboto {
  margin-bottom: 2px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #a93e3c !important;
}

.selectable-chip {
  margin: 10px 20px 10px 20px !important;
  border: 1px solid #a93e3c90 !important;
}

.not-selected {
  background-color: transparent !important;
}

.accordian-summary {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  z-index: 10000;
}

.accordian-details {
  border-top: 1px dashed #acabac;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.accordian-details::before {
  background-image: url("../../assets/Images/filter.jpg");
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.1;
  content:""
}

.accordian-summary::before {
  background-image: url("../../assets/Images/filter.jpg");
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.05;
  content:""
}

.accordian-root {
  margin-top: 30px;
  width: 95%;
}

.accordian-title {
  font-weight: 600 !important;
  align-self: center;
}

.search-button {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.search-input {
  width: 100%;
}
